export const defaultLangKey = 'en'
// 支持的语言
export const LANGS = [{
  label: 'English',
  value: LANG_KEY.en,
}, {
  label: '繁体中文',
  value: LANG_KEY.tw,
}]

export const getLang = () => {
  return window.localStorage.getItem(CACHE_KEY) || LANG_KEY[defaultLangKey]
}

export const setLang = (lang) => {
  let url = window.location.href
  if (lang == "zh-tw") {
    url = url.replaceAll("/#/zh-cn", "/#/zh-tw")
    url = url.replaceAll("/#/en-us", "/#/zh-tw")
  }
  if (lang == "zh-hans") {
    url = url.replaceAll("/#/zh-tw", "/#/zh-cn")
    url = url.replaceAll("/#/en-us", "/#/zh-cn")
  }
  if (lang == "en-US") {
    url = url.replaceAll("/#/zh-tw", "/#/en-us")
    url = url.replaceAll("/#/zh-cn", "/#/en-us")
  }
  if (url.indexOf("/#/zh-cn/") < 0 && url.indexOf("/#/zh-tw/") < 0 && url.indexOf("/#/en-us/") < 0) {
    const arr = url.split("#")
    if (lang == "zh-tw") {
      url = arr[0] + "#/zh-tw/swapV2/changelog"
    }
    if (lang == "en-hans") {
      url = arr[0] + "#/zh-cn/swapV2/changelog"
    }
    if (lang == "en-US") {
      url = arr[0] + "#/en-us/swapV2/changelog"
    }
  }
  window.location.href = url
  window.location.reload()
  window.localStorage.setItem(CACHE_KEY, lang)
}

export const getLangKeyByLang = (lang) => {
  // alert(lang)
  let key = defaultLangKey
  Object.keys(item => {
    if (LANG_KEY[item] === lang) {
      key = item
    }
  })
  return key
}



