const Auth = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/authentication-interface.html`,
  "name": "鑒權服務",
  "content": JSON.parse(aaa)
}


export const commonConfig = [
  Introduce,
  ChangeLog,
  QandA,
  Basic,
  Authentication,
  // Permission,
  Account,
  Wallet,
  SubAccountContent,
  Auth,
]


